import { useState } from "react";
import { Button, Card, Col, ListGroup } from "react-bootstrap";
import { Link } from "react-router-dom";

function Favorite({flowerList, setMonth, setDate}){
	let localList=JSON.parse(localStorage.getItem('flower_favorite'));
	let [stateList, setStateList] = useState([...localList]);

	const addFavorite = (name, month, date) => {
		setStateList([...localList,{name: name, month: month, date: date}]);

		localList.push({name: name, month: month, date: date});
		localList=[...new Set(localList.map(JSON.stringify))].map(JSON.parse);//
		localStorage.setItem('flower_favorite',JSON.stringify(localList));
	
		alert(' !');
	}
	const delFavorite = (month, date) => {
		let result = localList.findIndex((data) => data.month == month && data.date == date );
		if(result >= 0){
			localList.splice(result,1);

			let copy=[...stateList];
			copy.splice(result,1)
			setStateList(copy);
		}

		localStorage.setItem('flower_favorite',JSON.stringify(localList));
		alert(' !');
	}
	return(
		<div className='box_favorite'>
			<ButtonType stateList={stateList} name={flowerList.flowNm._text} month={flowerList.fMonth._text} date={flowerList.fDay._text} addFavorite={addFavorite} delFavorite={delFavorite}></ButtonType>
			<Card>
				<Card.Header as="h5"></Card.Header>
				<ListGroup variant="flush" className='list_favorite'>
					{
						stateList.length == 0
						?<ListGroup.Item> !</ListGroup.Item>
						:stateList?.map((data,i)=>{
							return(
								<ListGroup.Item key={'list_'+i}>
									<Link to='/' onClick={()=>{setMonth(data.month); setDate(data.date); console.log(data.month, data.date)}}>
										<p>{data.name}</p>
										<span className='date'>{data.month}.{data.date}</span>
									</Link>
								</ListGroup.Item>
							)
						})
					}
				</ListGroup>
			</Card>
		</div>
	)
}

function ButtonType({stateList, name, month, date, addFavorite, delFavorite}){
	let result=stateList?.findIndex((data)=> data.month == month && data.date == date);
	if(result < 0){
		return(
			<Button variant="outline-warning" size="lg" className='btn_favorite' onClick={()=>{addFavorite(name,month,date)}}>
				 
			</Button>
		)
	}else{
		return(
			<Button variant="warning" size="lg" className='btn_favorite' onClick={()=>{delFavorite(month,date)}}>
				 
			</Button>
		)
	}
}
export default Favorite;