import { useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";

function Search({p_month, p_date, setMonth, setDate}){
	let [calendar, setCalendar] = useState({month:p_month,day:p_date});
	let month=[];
	for(let m=1; m<=12; m++){
		month.push(m.toString());
	}

	let days=[];
	let date=new Date(2023,calendar.month,0).getDate();
	for(let d=1; d<=date; d++){
		days.push(d.toString());
	}

	return(
		<div className='box_search'>
			<Form className='clear'>
				<Row>
				<Col md={12} xs={6} className="mb-2">
					<Form.Group controlId='calendar_month' className='calendar_group'>
						<div className='select'>
							<Form.Select aria-label='Default select' value={calendar.month}
								onChange={(e)=>{setCalendar({...calendar,month:e.target.value})}}>-
								{
									month.map((item)=>{
										return(<option value={item} key={'month_'+item}>{item}</option>)
									})
								}
							</Form.Select>
						</div>
						<Form.Label></Form.Label>
					</Form.Group>
				</Col>
				<Col md={12} xs={6} className="mb-2">
					<Form.Group controlId='calendar_day' className='calendar_group'>
						<div className='select'>
							<Form.Select aria-label='Default select' value={calendar.day}
								onChange={(e)=>{setCalendar({...calendar,day:e.target.value})}}>
								{
									days.map((item)=>{
										return(<option value={item} key={'day_'+item}>{item}</option>)
									})
								}
							</Form.Select>
						</div>
						<Form.Label></Form.Label>
					</Form.Group>
				</Col>
				</Row>
				<Button variant="danger" size="lg" className='btn_search' onClick={()=>{setMonth(calendar.month); setDate(calendar.day);}}>
					 
				</Button>
			</Form>
		</div>
	)
}

export default Search;