import bg from './../img/img_bg.jpg';
import { Card, Carousel, Col, Row } from 'react-bootstrap';
import Favorite from './Favorite';
import Search from './Search';

//library
import { useEffect, useState } from 'react';
import axios from 'axios';
import convert from 'xml-js';
import Loading from './Loading';

const serviceKey='ZQMkETPY%2BBGvCGAahw9ja3VlO1E%2B4wOjHPR%2FQgcir1AZpeYU8EAGD%2B%2B%2BZSV3gbpkO8%2B3d8QeJPY6Z8my7jjb6g%3D%3D';

function Detail(){
	const [flowerList, setFlowerList] = useState([]);
	const [loading, setLoading] = useState(false);

	const [month, setMonth]=useState(new Date().getMonth()+1);
	const [date, setDate]=useState(new Date().getDate());

	const getFlower = async (month, date) =>{
		let getFlowerURL = `http://apis.data.go.kr/1390804/NihhsTodayFlowerInfo01/selectTodayFlower01?serviceKey=${serviceKey}&fMonth=${month}&fDay=${date}`;
		await axios.get(getFlowerURL)
		.then((result)=>{
			setFlowerList(JSON.parse(convert.xml2json(result.data, {compact: true, spaces: 4}))['document']['root']['result']);
			setLoading(true);
		})
		.catch((e)=>{
			console.log(': '+ e);
		})
	} 

	useEffect( ()=>{
		getFlower(month, date);

		if(localStorage.getItem('flower_favorite')==null){
			localStorage.setItem('flower_favorite',JSON.stringify([]));
		}

		return ()=>{
			setLoading(false);
		}
	},[]);

	useEffect(()=>{
		getFlower(month, date);

		return ()=>{
			setLoading(false);
		}
	},[month, date]);

	return(
		<>
			{
				loading ?
				<Row>
					<Col md={4} className='col_left'>
						<Favorite flowerList={flowerList} setMonth={setMonth} setDate={setDate}></Favorite>
						<Search p_month={month} p_date={date} setMonth={setMonth} setDate={setDate}></Search>
					</Col>
					<Col md={8} className='col_flower'>
						<div className='flower_title'>
							<h1>{month} {date}  </h1>
						</div>
						<Carousel>
							{
								flowerList.imgUrl1 !== undefined
								?<Carousel.Item>
									<div className='img back_img' style={{backgroundImage:`url(${flowerList.imgUrl1._cdata})`}}>
										<img src={bg} alt={flowerList.flowNm._text} className='basic_img'/>
									</div>
								</Carousel.Item>
								:null
							}
							{
								flowerList.imgUrl2 !== undefined
								?<Carousel.Item>
									<div className='img back_img' style={{backgroundImage:`url(${flowerList.imgUrl2._cdata})`}}>
										<img src={bg} alt={flowerList.flowNm._text} className='basic_img'/>
									</div>
								</Carousel.Item>
								:null
							}
							{
								flowerList.imgUrl3 !== undefined
								?<Carousel.Item>
									<div className='img back_img' style={{backgroundImage:`url(${flowerList.imgUrl3._cdata})`}}>
										<img src={bg} alt={flowerList.flowNm._text} className='basic_img'/>
									</div>
								</Carousel.Item>
								:null
							}
						</Carousel>
						<Card>
							<Card.Body>
								<Card.Title>{flowerList.flowNm._text}</Card.Title>
								<Card.Subtitle className="mb-2 text-muted"> {flowerList.fSctNm._text}</Card.Subtitle>
								<Card.Subtitle className="mb-2 text-muted"> {flowerList.fEngNm._text}</Card.Subtitle>
								<Card.Text>{flowerList.fContent._cdata}</Card.Text>
							</Card.Body>
						</Card>
						<Card>
							<Card.Header as="h5"></Card.Header>
							<Card.Body>
								<Card.Text>{flowerList.fUse._cdata}</Card.Text>
							</Card.Body>
						</Card>
						<Card>
							<Card.Header as="h5"></Card.Header>
							<Card.Body>
								<Card.Text>{flowerList.fGrow._cdata}</Card.Text>
							</Card.Body>
						</Card>
						<Card>
							<Card.Header as="h5"></Card.Header>
							<Card.Body>
								<Card.Text>{flowerList.fType._cdata}</Card.Text>
							</Card.Body>
						</Card>
					</Col>
				</Row>
				:<Loading></Loading>
			}
		</>
	)
}
export default Detail;